@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: 48px;
}

h4 {
  font-size: 22px;
}

form input {
  width: 100%;
  height: 40px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
  margin-top: 10px;
}
form select {
  margin-top: 10px;
}
form textarea {
  width: 100%;
  min-height: 140px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 16px 24px;
  line-height: 24px;
  resize: vertical;
}

button {
  cursor: pointer;
}

.button {
  cursor: pointer;
  width: 100%;
  height: 40px;
  background: #ba151c;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: normal;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
  margin-bottom: 10px;
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}
.back-link:hover {
  opacity: 0.8;
}
.back-link svg {
  margin-right: 8px;
}

.btn-secundary-angeluz {
  background: #f3b838;
  color: #fff;
  padding: 10px 45px;
  font-size: 16px;
  border-radius: 6px;
  max-width: 180px;
  text-align: center;
}
.btn-secundary-angeluz:hover {
  text-decoration: none;
  color: #fff;
}

.btn-primary-angeluz {
  background: #af1013;
  border: 0;
  padding: 10px 45px;
  font-size: 16px;
  border-radius: 6px;
  color: #fff;
  text-decoration: none;
}
.btn-primary-angeluz:hover {
  color: #fff;
  text-decoration: none;
  opacity: 0.8;
}

#wrapper.toggled #sidebar-wrapper {
  margin-left: -15rem;
}

.title-system {
  font-size: 28px;
  margin-left: 10px;
}

