.grid-case{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 12px;
}
.box-case{
    background-color: #eee;
    border-radius: 6px;
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    position: relative;
}
.box-case a{
    color: #333;    
}
.box-case a:hover{
